@import "../styles/constants"

.dialogbox
	transition: .5s all ease
	position: absolute
	display: flex
	align-items: center
	flex-direction: column
	left: 5px
	right: 5px
	animation: active
	animation-duration: .5s
	animation-fill-mode: forwards
	border-radius: 5px
	user-select: none
	text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.6)

	z-index: 3
	&__next
		z-index: 1
		position: absolute
		top: 0
		left: 0
		width: 100%
		height: 100%
	&__header
		display: flex
		justify-content: space-between
		align-items: center
		color: white
		font-weight: bold
		padding: 0 0
	&__speaker
		font-size: 35px
		padding: 0 25px
	&__lines
		display: flex
		flex-direction: column
		align-items: flex-end
	&__line
		margin-top: 1px
		width: 200px
		height: 4px
		background: linear-gradient(270deg, #A5D9DA 0%, rgba(166, 217, 219, 0) 100%)
		&_small
			width: 100px
			background: linear-gradient(270deg, #FFD25F 0%, rgba(218, 165, 165, 0) 100%)
	&__save
		pointer-events: initial
		display: block
		transition: .4s all ease
		opacity: .4
		cursor: pointer
		width: 25px
		position: absolute
		right: 10px
		&:hover
			opacity: 1
	&__navigation
		display: flex
		position: absolute
		right: 50px
	&__menu
		transition: .3s all ease
		cursor: pointer
		opacity: .6
		&:not(:last-child)
			margin-right: 10px
		&:hover
			opacity: 1
	&__body
		margin-top: 15px
		line-height: 1.3
		padding:  35px 50px
		background-color: $dialogboxColor
		color: $textColor
		width: 95%
		height: 160px
		border-radius: 10px
		border-top: 2px solid $dialogboxColor
		backdrop-filter: blur(10px)
		box-sizing: border-box
	&__hide
		animation: hide
		animation-duration: .5s
		animation-fill-mode: forwards

@keyframes active
	0%
		bottom: -50%
	100%
		bottom: 25px

@keyframes hide
	0%
		bottom: 25px
	100%
		bottom: -50%

.dialogbox__twok
	display: block

.dialogbox__fullhd
	display: block

.dialogbox__hd
	.dialogbox
		&__body
			font-size: 22px

.dialogbox__mobile
	.dialogbox
		&__speaker
			font-size: 22px
		&__body
			padding: 15px 25px
			font-size: 16px
			height: 120px

.strings
	font-weight: 700
	display: flex
	align-items: center
	justify-content: center
	font-size: 22px
	position: absolute
	left: 5px
	right: 5px
	bottom: 25px
	width: 95%
	height: 30%
	text-transform: uppercase
	z-index: 2
	color: white
	text-align: center
	margin: 0 auto
	user-select: none
	line-height: 150%
	&__center
		top: 50%
		transform: translateY(-50%)
	&__shadow
		position: absolute
		bottom: 0
		width: 100%
		height: 35%
		z-index: 1
		background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(255,255,255,0) 100%)